import React from 'react';
import './App.css';

class Board extends React.Component {
  constructor(props) {
    super(props);
    let stuff = [];
    for (let i = 0; i < 50; i++) {
      let left = Math.floor(Math.random() * 10) * 50 + 25
      let top = Math.floor(Math.random() * 10) * 50 + 25
      if (50 + 50 >= left && 50 <= left + 10) {
        if (50 + 50 >= top && 50 <= top + 10) {
          i--;
          continue;
        }
      }
      if (i > 0) {
        let cont = false;
        stuff.forEach((stuff) => {
          if (stuff.left + 10 >= left && stuff.left <= left + 10) {
            if (stuff.top + 10 >= top && stuff.top <= top + 10) {
              i--;
              cont = true;
            }
          }
        });
        if (cont) continue;
      }
      stuff.push({ top, left, collected: false })
    }
    let map = []
    let VWalls = []
    let HWalls = []
    VWalls.push({ top: 50, left: 50, height: 400 })
    HWalls.push({ top: 50, left: 50, width: 400 })
    VWalls.push({ top: 100, left: 450, height: 400 })
    HWalls.push({ top: 450, left: 50, width: 350 })

    VWalls.push({ top: 150, left: 100, height: 250 })
    HWalls.push({ top: 100, left: 100, width: 300 })
    VWalls.push({ top: 100, left: 400, height: 300 })
    HWalls.push({ top: 400, left: 100, width: 300 })


    VWalls.push({ top: 150, left: 150, height: 200 })
    HWalls.push({ top: 150, left: 150, width: 200 })
    VWalls.push({ top: 150, left: 350, height: 150 })
    HWalls.push({ top: 350, left: 150, width: 200 })

    VWalls.push({ top: 200, left: 200, height: 100 })
    HWalls.push({ top: 200, left: 200, width: 50 })
    HWalls.push({ top: 250, left: 200, width: 50 })
    VWalls.push({ top: 200, left: 300, height: 100 })
    HWalls.push({ top: 300, left: 200, width: 100 })
    map.push({ HWalls, VWalls })
    VWalls = []
    HWalls = []
    VWalls.push({ top: 0, left: 50, height: 450 })
    VWalls.push({ top: 0, left: 150, height: 450 })
    VWalls.push({ top: 0, left: 250, height: 450 })
    VWalls.push({ top: 0, left: 350, height: 450 })
    VWalls.push({ top: 0, left: 450, height: 450 })
    VWalls.push({ top: 50, left: 100, height: 450 })
    VWalls.push({ top: 50, left: 200, height: 450 })
    VWalls.push({ top: 50, left: 300, height: 450 })
    VWalls.push({ top: 50, left: 400, height: 450 })
    map.push({ HWalls, VWalls })
    VWalls = []
    HWalls = []
    HWalls.push({ left: 0, top: 50, width: 450 })
    HWalls.push({ left: 0, top: 150, width: 450 })
    HWalls.push({ left: 0, top: 250, width: 450 })
    HWalls.push({ left: 0, top: 350, width: 450 })
    HWalls.push({ left: 0, top: 450, width: 450 })
    HWalls.push({ left: 50, top: 100, width: 450 })
    HWalls.push({ left: 50, top: 200, width: 450 })
    HWalls.push({ left: 50, top: 300, width: 450 })
    HWalls.push({ left: 50, top: 400, width: 450 })
    map.push({ HWalls, VWalls })


    let level = 1
    this.state = {
      Player: { top: 0, left: 0, direction: {} },
      intervalID: 0, map, level, speed: 100,
      stuff, VWalls: map[level].VWalls, HWalls: map[level].HWalls, Enemy: { top: 250, left: 200, canContinue: false, direction: {} }
    }
  }
  changeLevel(level) {
    this.setState({ VWalls: this.state.map[level].VWalls, HWalls: this.state.map[level].HWalls, level })
  }
  shuffle(array) {
    for (let i = array.length - 1; i > 0; i--) {
      let j = Math.floor(Math.random() * (i + 1));
      [array[i], array[j]] = [array[j], array[i]];
    }
  }
  move() {
    let Player = this.state.Player;
    let left = Player.left + Player.direction.left;
    let top = Player.top + Player.direction.top;
    let speed = this.state.speed;
    let Enemy = this.state.Enemy;

    if (left + 49 >= Enemy.left && left <= Enemy.left + 49) {
      if (top + 49 >= Enemy.top && top <= Enemy.top + 49) {
        clearInterval(this.state.intervalID);
        this.props.changeScreen("Lose")
      }
    }
    let wallstest = true;
    if (left < 0 || left > 450 || top < 0 || top > 450) { wallstest = false; }
    this.state.VWalls.forEach((wall) => {
      if (left + 40 >= wall.left && left + 3 <= wall.left) {
        if (top + 40 >= wall.top && top <= wall.top + wall.height - 10) {
          wallstest = false;
        }
      }
    });
    this.state.HWalls.forEach((wall) => {
      if (left + 40 >= wall.left && left <= wall.left + wall.width - 10) {
        if (top + 40 >= wall.top && top <= wall.top - 10) {
          wallstest = false;
        }
      }

    });
    if (wallstest) {
      this.state.stuff.forEach((stuff) => {
        //collision
        if (stuff.collected) { return; }
        if (left + 50 >= stuff.left && left <= stuff.left + 10) {
          if (top + 50 >= stuff.top && top <= stuff.top + 10) {
            stuff.collected = true;
            let win = true;
            this.state.stuff.forEach((stuff) => { if (!stuff.collected) { win = false; } });
            if (win) { clearInterval(this.state.intervalID); this.props.changeScreen("Win") }
            speed = Math.max(speed - 2, 0);
          }
        }
      });
    } else {
      left = Player.left;
      top = Player.top;
    }
    if (Enemy.canContinue === false) {
      let direction = [
        { top: 0, left: -10 },
        { top: 0, left: 10 },
        { top: 10, left: 0 },
        { top: -10, left: 0 },
      ];
      this.shuffle(direction);

      for (let i = 0; i < 4; i++) {
        if (direction[i].top === -Enemy.direction.top && direction[i].left === -Enemy.direction.left) { Enemy.direction.top = 0; Enemy.direction.left = 0; Enemy.canContinue = false; continue; }
        let top = Enemy.top + direction[i].top
        let left = Enemy.left + direction[i].left
        if (top < 0 || top > 450 || left < 0 || left > 450) { continue; }
        let wallstest = true;
        this.state.VWalls.forEach((wall) => {
          if (left + 40 >= wall.left && left + 3 <= wall.left) {
            if (top + 40 >= wall.top && top <= wall.top + wall.height - 10) {
              wallstest = false;
            }
          }
        });
        this.state.HWalls.forEach((wall) => {
          if (left + 40 >= wall.left && left <= wall.left + wall.width - 10) {
            if (top + 40 >= wall.top && top <= wall.top - 10) {
              wallstest = false;
            }
          }
        });
        if (!wallstest) {
          continue;
        }
        Enemy = { top, left, direction: direction[i], canContinue: true };
        break;
      }
    } else {
      let top = Enemy.top + Enemy.direction.top
      let left = Enemy.left + Enemy.direction.left
      if (top < 0 || top > 450 || left < 0 || left > 450) {
        Enemy.canContinue = false;
      } else {
        let wallstest = true;
        this.state.VWalls.forEach((wall) => {
          if (left + 40 >= wall.left && left + 3 <= wall.left) {
            if (top + 40 >= wall.top && top <= wall.top + wall.height - 10) {
              wallstest = false;
            }
          }
        });
        this.state.HWalls.forEach((wall) => {
          if (left + 40 >= wall.left && left <= wall.left + wall.width - 10) {
            if (top + 40 >= wall.top && top <= wall.top - 10) {
              wallstest = false;
            }
          }
        });
        if (!wallstest) {
          Enemy.canContinue = false;
        } else {
          Enemy = {
            top: Enemy.top + Enemy.direction.top,
            left: Enemy.left + Enemy.direction.left,
            canContinue: true, direction: Enemy.direction
          }
        }
      }
    }
    this.setState({ Player: { left, top, direction: Player.direction }, Enemy, speed })

  }

  keyHandler(event) {
    switch (event.key) {
      case ('c'):
        this.changeLevel(Math.max(this.state.level - 1, 0))
        break;
      case ('v'):
        this.changeLevel(Math.min(this.state.level + 1, 2))
        break;
      default:
    }
    let Player = this.state.Player;
    let direction = {};
    let movekeys = ["ArrowLeft", "ArrowRight", "ArrowUp", "ArrowDown"];
    if (movekeys.includes(event.key)) {
      switch (event.key) {
        case "ArrowLeft":
          direction = { left: -10, top: 0 }
          break;
        case "ArrowRight":
          direction = { left: +10, top: 0 }
          break;
        case "ArrowUp":
          direction = { left: 0, top: -10 }
          break;
        case "ArrowDown":
          direction = { left: 0, top: +10 }
          break;
        default:
      }
      let left = Player.left + direction.left;
      let top = Player.top + direction.top;
      let wallstest = true;
      if (left < 0 || left > 450 || top < 0 || top > 450) { wallstest = false; }
      this.state.VWalls.forEach((wall) => {
        if (left + 40 >= wall.left && left + 3 <= wall.left) {
          if (top + 40 >= wall.top && top <= wall.top + wall.height - 10) {
            wallstest = false;
          }
        }
      });
      this.state.HWalls.forEach((wall) => {
        if (left + 40 >= wall.left && left <= wall.left + wall.width - 10) {
          if (top + 40 >= wall.top && top <= wall.top - 10) {
            wallstest = false;
          }
        }
      });
      if (wallstest) {
        Player.direction = direction;
        clearInterval(this.state.intervalID);
        let intervalID = setInterval(this.move.bind(this), this.state.speed);
        this.setState({ intervalID})
      }
    }
  }
  render() {
    return (
      <div>
        <div id="focus" onLoad={() => { document.getElementById("focus").focus(); }} onKeyDown={(event) => (this.keyHandler(event))} tabIndex='99' style={{ height: '500px', width: '500px', border: '5px black solid' }}>
          <Character
            top={this.state.Player.top}
            left={this.state.Player.left}
          />{
            this.state.stuff.map(stuff => {
              return (
                stuff.collected ? <div style={{ display: 'none' }}></div> :
                  <Stuff
                    top={stuff.top}
                    left={stuff.left}
                  />
              )
            })
          }{
            this.state.VWalls.map(wall => {
              return (
                <VerticalWall
                  top={wall.top}
                  left={wall.left}
                  height={wall.height}
                />
              )
            })

          }
          {
            this.state.HWalls.map(wall => {
              return (
                <HorizontalWall
                  top={wall.top}
                  left={wall.left}
                  width={wall.width}
                />
              )
            })

          }
          <Enemy
            top={this.state.Enemy.top}
            left={this.state.Enemy.left}
          />

          {/* <div>{this.state.top}</div>
          <div>{this.state.left}</div>
          <div>{this.state.stuff.top}</div>
          <div>{this.state.stuff.left}</div> */}
        </div>
        <div>
          <div>{this.state.speed}</div>
        </div></div>)
  }
}
class Character extends React.Component {
  render() {
    return (
      <div style={{ position: 'absolute', top: this.props.top + 5, left: this.props.left + 5, transition: 'all .01s', height: '50px' }}>
        <img style={{ display: 'block', height: '50px', width: '50px' }} src='./index.png' alt='' />
      </div>)
  }
}
class Enemy extends React.Component {
  render() {
    return (
      <div style={{ position: 'absolute', top: this.props.top + 5, left: this.props.left + 5, transition: 'all .01s', height: '50px' }}>
        <img style={{ display: 'block', height: '50px', width: '50px' }} src='./enemy.png' alt='' />
      </div>
    )
  }

}
class Stuff extends React.Component {
  render() {
    return (
      <div style={{ position: 'absolute', top: this.props.top + 5, left: this.props.left + 5, transition: 'all .01s', height: '10px' }}>
        <img style={{ display: 'block', height: '10px', width: '10px' }} src='./Stuff.png' alt='' />
      </div>

    )
  }
}
class VerticalWall extends React.Component {
  render() {
    return (
      <div style={{ zIndex: 99, position: 'absolute', top: this.props.top + 5, left: this.props.left + 5, transition: 'all .01s', height: this.props.height, border: '0px', borderLeft: '3px solid' }}></div>
    )
  }
}
class HorizontalWall extends React.Component {
  render() {
    return (
      <div style={{ zIndex: 99, position: 'absolute', top: this.props.top + 5, left: this.props.left + 5, transition: 'all .01s', height: '0px', width: this.props.width, border: '0px', borderTop: '3px solid' }}></div>
    )
  }
}
class Win extends React.Component {
  render() {
    return (
      <div>
        You Win
        <button onClick={() => { this.props.changeScreen("Board") }}>Play Again</button>
      </div>
    )
  }
}
class Lose extends React.Component {
  render() {
    return (
      <div>
        You Lose
        <button onClick={() => { this.props.changeScreen("Board") }}>Play Again</button>
      </div>
    )
  }
}


class Base extends React.Component {
  constructor(props) {
    super(props);
    this.state = { screen: "Board" };
  }

  render() {
    return (
      <div>
        {this.state.screen === "Board" ? <Board changeScreen={(screen) => { this.setState({ screen }) }} /> : <span />}
        {this.state.screen === "Lose" ? <Lose changeScreen={(screen) => { this.setState({ screen }) }} /> : <span />}
        {this.state.screen === "Win" ? <Win changeScreen={(screen) => { this.setState({ screen }) }} /> : <span />}
      </div>
    )
  }
}


function App() {
  return (
    <Base />
  );
}

export default App;
